import { Component,NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiservicesService } from 'src/app/provider/apiservices.service';
import { DataTransferService } from 'src/app/services/data-transfer.service';
import { URLS } from 'src/assets/constant';

@Component({
  selector: 'app-birthday-anniversary-list',
  templateUrl: './birthday-anniversary-list.page.html',
  styleUrls: ['./birthday-anniversary-list.page.scss'],
})
export class BirthdayAnniversaryListPage implements OnInit {
  tab1Data: any;
  birthdayAniversaryData: any;
  errorMessage: any;

  constructor(private zone:NgZone,private router:Router,private sendData:DataTransferService,
    private apiservices:ApiservicesService,) { 
      this.tab1Data=this.sendData.alldata;
    }

  ngOnInit() {
  this.birthdayAniversaryList();
  }
  gotoDetail(item){
    this.sendData.alldata=item;
    this.router.navigate(['/birthday-aniversray-detail']);
  }

  birthdayAniversaryList(){
    this.zone.run(()=>{
      this.apiservices.showLoader();
        let apiKey={  
          "event_type":this.tab1Data.event_type
          };
      console.log("resend OTP by Voice Call, api key", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.birthdayAnniversaryApi, apiKey).subscribe((result) => {
        console.log("resend OTP by Voice Call, api response==",result);
        this.apiservices.hideLoader();
        this.birthdayAniversaryData=result.data;
        if(result.status==1 || result.status=='1'){
         
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'greenBg');
          // this.router.navigate(['/qualification-priview']);
          this.errorMessage='';
        }else{
          this.errorMessage=result.message;
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
        }
      },err=>{
        this.apiservices.hideLoader();
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
      });


  }

}
